import type from '../type.js'
import axios from 'axios'

const state = {
	goods:[],

	foodshow : false,
	// carshow : true,
	food:{}
	
}

//返回数据
const getters={
	goods(state){
		return state.goods;
	},
	food(state){
		return state.food;
	},
	foodshow(state){
		return state.foodshow;
	}
	// ,
	// carshow(state){
	// 	return state.carshow;
	// },
	// carhide(state){
	// 	return state.carhide;
	// }
}

const actions = {
	getGoods({commit,state}){
		axios.get('../../../static/data.json').then( resp => {
			if( resp.data.errno === 0){
				commit(type.GET_GOODS,resp.data.goods)
			}
		})
	},
	showfood({commit},food){
		commit(type.SHOW_FOOD,food);
	},
	hidefood({commit}){
		commit(type.HIDE_FOOD);
	}
	// ,
	// carshow({commit}){
	// 	commit(type.CAR_SHOW);
	// },
	// carhide({commit}){
	// 	commit(type.CAR_HIDE);
	// },
}

const mutations = {
	[type.GET_GOODS](state,data){
		state.goods = data;
	},
	[type.SHOW_FOOD](state,food){
		state.foodshow = true;
		state.food = food;
	},
	[type.HIDE_FOOD](state){
		state.foodshow = false;
	}
	// ,
	// [type.CAR_SHOW](state){
	// 	state.carshow = true;
	// },
	// [type.CAR_HIDE](state){
	// 	state.carshow = false;
	// }
}

export default {
	state,
	getters,
	actions,
	mutations
}


