<template>
	
	<div class="cartcontrol">
	<div class="carcar">
	
		<div class="reduce">
		<transition name='move'>
			<i class="iconfont icon-jianhao" v-show='food.count' @click.stop='decremrnt'></i>
		</transition>
		</div>
		
		<div class="countfood">
			<span v-show='food.count'>{{food.count}}</span>
		</div>
	
		<div class="add">
			<i class="iconfont icon-jiahao" @click.stop='increment'></i>
		</div>
	</div>
		
		
	</div>
	
</template>

<script>
	import Vue from 'vue'
	import {mapGetters} from 'vuex'

	export default {
		props:['food'],
		methods:{
			increment(){
				if( !this.food.count){
					Vue.set(this.food , 'count' ,1);
				}else{
					this.food.count ++;
				}
			},
			decremrnt(){
				if(this.food.count){
					this.food.count --;
				}
			}

		}
	}
</script>

<style lang='less' scoped>
	.cartcontrol{

		width: 1rem;
		height: 0.6rem;
		display: flex;
			

		.carcar{
			

			width: 1.2rem;
			display: flex;
			position: relative;
			
			.reduce{
.move-enter-active, .move-leave-active {
            	transition: all 0.5s;
        	}
        	.move-leave-active {
            	transition: all 0.5s;
            	transform:translate(30px) rotate(90deg);
        	}
        // count>0时候的动画，v-show显示时的动画效果
        	.move-enter, .move-leave-active {
            	transform:translate(20px) rotate(90deg);
            	opacity: 0
        	}
				
				

				// display: inline-block;
				.icon-jiahao{
				color:#00a0dc;
				}
				.icon-jianhao{
				color:#00a0dc
				}
				position: absolute;
		}
	
		.countfood{
			width: 0.8rem;
			// position: absolute;
			// display: inline-block;
			text-align: center;
				span{
					display: inline-block;
					text-align: center;
					width: 0.8rem;
					font-size: 0.3rem;
					color:rgb(147, 153, 159);
					margin-left: 0.15rem;
					// color:red;
					// line-height: rem;
				}
		}

		.add{
			.icon-jiahao{
				color:#00a0dc;
			}
				// width: 0.5rem;
				// display: inline-block;
		}
		}
	
	
	}
	
</style>


