<template>

	<div class="ratings">
		<div class="score">
			<div class="scoreleft">
			<div>
				<p>{{seller.score}}</p>
				<p>综合评分</p>
				<p>高于周边商家{{seller.rankRate}}%</p>
			</div>
				
			</div>
			<div class="scoreright">
			<div class="">
			<ul>
				<li>
					<span class="tot">服务态度：</span><star :score= 'seller.serviceScore'></star><span class="sco">{{seller.serviceScore}} </span>
				</li>

				<li>
					<span class="tot">菜品评分：</span><star :score= 'seller.foodScore'></star><span class="sco">{{seller.foodScore}}</span>
				</li>

				<li>
					<span class="tot">送达时间：</span><span class="time">{{seller.deliveryTime}}分钟</span>
				</li>
			</ul>
				
			</div>
				
			</div>
		</div>

		<div class="strip"></div>

	
		<div class="conrating">
			<div class="but">
				<div class="btn1" @click="getall" :class="{'comcolor':sizeone}">全部 <span>{{seller.ratingCount}}</span></div>
				<div class="btn2" @click="ok" :class="{'comcolor':sizetwo}">满意 <span>{{good}}</span></div>
				<div class="btn3" @click="no" :class="{'comcolor':sizethree}">不满意 <span>{{seller.ratingCount-good}}</span></div>
				
			</div>

			<div class="sel" >
				<i class="iconfont icon-duihao" :class="{'checked':flag}" @click="changeFalg"></i>
				<span :class="{'checked':flag}">只看有内容的评价</span>
			</div>
		<div class="menu-wrapper"  ref = "menu1Wrapper">
			<div class="user">
				<div class="userrat" v-for='(v,k) in ratLists' :key='k' v-show="all || yes === v.rateType">
					
						<div>
							<img :src="v.avatar" alt="">
						</div>
						<div class="userright">
							<p>{{v.username}} <span class="rate">{{changeTime(v.rateTime)}}</span></p>
							<p><star :score='v.score'></star></p>
							<div class="arrival" v-if='v.deliveryTime'>{{v.deliveryTime}}分钟送达</div>
							<p>{{v.text}}</p>
							
								<i v-if="v.rateType == 0" class="iconfont icon-damuzhion"></i>
								<i v-else="v.rateType == 1" class="iconfont icon-down"></i>
								<span v-if="k<3" v-for='(i,k) in v.recommend' :key='k' class="recom">
								{{i}}
								</span>	
							
							
						</div>
					
					
				</div>
				<div class="blank"></div>
			</div>

		
			
		</div>
		
	</div>

	</div>

</template>

<script>
	import {mapGetters} from "vuex"
	import star from '../star/star_s.vue'
	import BScroll from 'better-scroll'


	export default {
		data(){
			return {
				all:true,
				yes:-1,
				flag:false,
				sizeone:true,
				sizetwo:false,
				sizethree:false
			}
		},
		computed : {
			...mapGetters([
				// 'goods',
				'ratings',
				'seller'
			]),
			ratLists(){
				if(this.flag){
					return this.ratings.filter((item) => {
						return item.text
					})
				}
				return this.ratings;
			},
			good(){
				let arr=[];
				this.ratings.forEach(type=>{
					if(type.rateType==0){
						arr.push(type);
					}
				});
				// console.log(arr.length)
				return arr.length;

			}
		},
		components:{
			'star':star
		},
		created(){
			// this.$store.dispatch('getGoods');
			this.$store.dispatch('getRatings');
			this.$store.dispatch('getSeller');
			this.$nextTick( () => {
				setTimeout( () => {
					this._initMenu();
				},100)
			})


		},
		methods : {
			_initMenu(){
				this.menuBS = new BScroll(this.$refs.menu1Wrapper,{
					click:true
				});
				
			},
			refresh(){
				// console.log(1);
				setTimeout(() => {
					this.menuBS.refresh()
				},20)
			},
			ok(){
				// console.log(1)
				this.sizeone=false
				this.sizetwo=true
				this.sizethree=false
				this.yes = 0
				this.all = false
				this.refresh()
			},
			getall(){
				// console.log(1)
				this.sizeone=true
				this.sizetwo=false
				this.sizethree=false
				this.yes = -1
				this.all = true
				this.refresh()
			},
			no(){
				// console.log(1)
				this.sizeone=false
				this.sizetwo=false
				this.sizethree=true
				this.yes = 1
				this.all = false
				this.refresh()
			},
			changeFalg(){
				// console.log(1)
				// if( !e._constructed ){
				// 	return;
				// };
				
				this.flag =!this.flag
				this.refresh()
			},
			changeTime(time){
				let time1 = new Date(time);
				let year = time1.getFullYear();
				let month = time1.getMonth();
				let date = time1.getDate();
				let hour = time1.getHours();
				let minute = time1.getMinutes();
				let seconds = time1.getSeconds();
				let timechange = year+"-"+month+"-"+date+"-"+hour+":"+minute+":"+seconds;
				return timechange;
			}


		}
	}
</script>

<style lang='less' scoped>
	.score{
		display: flex;
		border-bottom: 2px solid #eee;
		.scoreleft{
				width: 2.5rem;
				height: 2rem;
				// background-color: #ccc;
				div{
					text-align: center;
					margin:0.36rem 0 0.36rem 0;
					border-right:1px solid #ccc; 
					p:nth-of-type(1){
						font-size: 0.48rem;
						color:rgb(255,153,0);
						line-height: 0.56rem;
					}
					p:nth-of-type(2){
						font-size: 0.24rem;
						color:rgb(7,17,27);
						line-height: 0.4rem;
					}
					p:nth-of-type(3){
						font-size: 0.2rem;
						color:rgba(7,17,27,0.5);
						line-height: 0.35rem;
					}

				}
		}
		.scoreright{
			margin: 0.3rem 0.2rem 0rem 0.2rem;
			div{
				// margin: 0.36rem 0.48rem 0.36rem 0.48rem;
				ul li{
					display: flex;
				}
				 .tot{
					font-size: 0.24rem;
					color:rgb(7,17,27);
					line-height: 0.56rem;
					
					
				}
				.time{
						font-size: 0.24rem;
						color:rgb(147,153,159);
						line-height: 0.56rem;
					}
				.sco{
						font-size: 0.24rem;
						color:rgb(255,153,0);
						line-height: 0.66rem;

					}
			}
		}


	}
	.strip{
			width: 100%;
			height: 0.5rem;
			background-color: #eee;
		}
	.menu-wrapper{
		height: 4.5rem;
		overflow: hidden;
		.user{
			.blank{
				width: 100%;
				height: 1.68rem;
			}
		}
		
	}
	
	
	.conrating{

		height: 6rem;
		// position: absolute;
		// z-index: -10;
		.but{
			width: 7rem;
			// background-color: #ccc;
			margin: 0.2rem auto;
			display: flex;
			font-size: 0.24rem;

			border-bottom: 1px solid #ddd;
			div{
				width: 1.5rem;
				height: 0.8rem;
				text-align: center;
				line-height: 0.8rem;
				margin-right: 0.3rem;
				margin-bottom: 0.3rem;

				// background-color: red;
			}
			.btn1{

				background-color: skyblue;
				color:#fff;
				&.comcolor{
					background-color: #2d8ced;
				}
			}

			.btn2{
				
				background-color: lightblue;
				color:#fff;
				&.comcolor{
					background-color: #2d8ced;
				}
			}
			// .btn1:active,.btn2:active,.btn3:active{
			// 	background-color:#52bbf2;
			// }
			// &.sizeone,&.sizetwo,&.sizethree{
			// 	background-color: blue;
			// }

			.btn3{

				background-color: #ddd;
				color:#aaa;
				&.comcolor{
					background-color: #2d8ced;
					color:#fff;
				}
			}
		}

		.sel{
			// width: 100%;
			height: 0.6rem;
			padding-left: 0.3rem;
			color:#ccc;
			font-size: 0.3rem;
			border-bottom: 1px solid #ddd;
			.checked{
				color:skyblue;
			}
		}
		.userrat{
			border-bottom: 1px solid #ddd;
			margin: 0.2rem;
			display: flex;
			// height: 2rem;
			img{
				border-radius: 0.5rem;
			}
			p:nth-of-type(1){
				font-size: 0.2rem;
				line-height: 0.24rem;
			}
			p:nth-of-type(3){
				font-size: 0.24rem;
				line-height: 0.36rem;

			}
			.userright{
				.arrival{
					font-size: 0.2rem;
					color:rgb(147,153,159);
					margin-top: -0.3rem;
					margin-left: 2rem;
				}
				.rate{
					padding-left: 2.5rem;
				}
				padding-left: 0.3rem;
				.recom{
				font-size: 0.18rem;
				color:rgb(147, 153, 159);
				// line-height: 0.1rem;
				border:1px solid #ccc;
				margin-right: 0.2rem;
				}
				.icon-damuzhion{
					color:skyblue;
				}
				.icon-down{
					color:#ccc;
				}
			}
			
		}

	}
</style>


