<template>
	<div class="header">
	    <!-- 模糊背景 -->
		<div class="bg">
			
		</div>

		<div class="container">
			<div class="headleft">
				<img :src="seller.avatar" alt="" class="avatar">
			</div>
			
			<div class="headright" @click='show'>
				<div class="brandcon">
					<img src="../imgs/brand.png" alt="" class="brand">
					<span class="title">{{seller.name}}</span>
				</div>
				
				<p class="title2">{{seller.description}}/{{seller.deliveryTime}}分钟送达</p>
				<div class="sub">
					<p class="title2" v-if="seller.supports"><img src="../imgs/decrease.png" alt="">&nbsp;{{seller.supports[0].description}}</p>
					<div class="fine">5个 ></div>
				</div>
			</div>
		</div>
	
		<div class="notice">
			<img src="../imgs/bulletin.png" alt="">
			<span>{{seller.bulletin}}</span>
			<!-- <span>></span> -->
		</div>
		<detail></detail>

	</div>	

	<!-- <div class="nav">
		
	</div> -->
	
</template>

<script>
	import {mapGetters} from 'vuex'
	import detail from '../detail/detail.vue'
	export default {
		computed:{
			...mapGetters([
				'seller', 
			])
		},
		components:{
			detail
		},
		created(){
			this.$store.dispatch('getSeller');
		},
		methods : {
			show(){
				// console.log(2);
				this.$store.dispatch('showdetail');
			}
		}
	}
</script>

<style lang='less' scoped>
	
	.header{
		height: 2.7rem;
		background:rgba(0, 0, 0, 0.5);
		overflow: hidden;
		// position: absolute;
		// z-index: -4;

		
		
		.bg{
			width: 100%;
			height: 2.6rem;

			background: url('../imgs/bg.png') no-repeat ;
			background-size: 100% 100%;
			
			position: absolute;
			z-index: -1;
			filter:blur(0.1rem);
			// opacity: 0.25;
		}
		.container{
			width: 100%;
			height: 2.68rem;
			display: flex;

			z-index: -1;
			.headleft{
				padding-top: 0.48rem;
				padding-left: 0.48rem;
			}
			.headright{
				// padding-top: 0.48rem;
				padding-left: 0.32rem;
				.sub{
					display: flex;
				}
				.fine{
					font-size: 0.24rem;
					width: 0.96rem;
					height: 0.48rem;
					background: rgba(0, 0, 0,0.5);
					text-align: center;
					line-height: 0.48rem;
					border-radius: 0.24rem;
					margin-left: 2rem;
					color: rgb(255,255,255);
				}
			}
			.brand{

				width: 0.7rem;
				line-height: 0.35rem;
			}
		}
		
		.avatar{
			
			width:1.28rem;
			height: 1.28rem;
			border-radius: 0.04rem;
			

		}
		.title{
			
			font-size: 0.32rem;
			color: rgb(255,255,255);
			font-weight: bold;
			line-height: 0.35rem;
		}
		.title2{
			
			font-size: 0.24rem;
			color: rgb(255,255,255);
			margin-top:0.1rem;

			line-height: 0.36rem;
		}
		.title2 img{
			width: 0.3rem;
		}
		.notice{
			width: 100%;
			height:0.56rem;
			// position: absolute;
			margin-top: -0.55rem;
			line-height: 0.56rem;
			background-color: rgba(7, 17, 27, 0.5);
			img{
				padding-left: 0.24rem;
				width: 0.6rem;
			}
			span:nth-of-type(1){
				// width: 1rem;
				display: inline-block;
				font-size: 0.2rem;
				color: rgb(255, 255, 255);
				width: 82%;
				height: 0.56rem;
				overflow: hidden;
				white-space: nowrap;
				text-overflow: ellipsis;
			}
			span:nth-of-type(2){
				color:rgb(255, 255, 255);
			}
		}


	}
</style>


