<template>
	<div class="goods">
	<div class="menu-wrapper"  ref = "menu1Wrapper">
		<div class="goods-menu">
			<ul>
				<li @click='selectMenu(k,$event)' v-for='(v,k) in goods' :key='k'  class="menu-list" :class="{'current' : k === currentIndex}">
					<span class="text">{{v.name}}</span>
				</li>
				<!-- <hr> -->

			</ul>
		</div>
	</div>
		
		<div class="menu-wrapper"  ref = "menu2Wrapper">
			<div class="goods-foods">
				<ul>
					<li v-for='(v,k) in goods' :key='k' class="foodlist food-list-hook">
						<p class="foodmenu">{{v.name}}</p>
						<!-- <p v-for=''></p> -->

							<div class="dish">
							<ul>
								<li v-for='i in v.foods' class="food" @click='showfood(i,$event)'>
								<div class="dishlist">
									<div>
									
										<img :src="i.image" alt="">
										
									</div>
									<div>
										<p class="iname">{{i.name}}</p>
										<p v-if='i.description' class="ides">{{i.description}}</p>
										<p class="isell">月售：{{i.sellCount}}份
										&nbsp;
										<span>好评率：{{i.rating}}%</span></p>
										
										<p class="ipri">￥{{i.price}}&nbsp;
										<span v-if='i.oldPrice' class="iold">{{i.oldPrice}}</span>
										</p>

									</div>
									<div class="cart">
										
										<Cartcontrol :food="i" class='carticon'></Cartcontrol>

									</div>
								</div>
								
								
								
								</li>
							</ul>
							</div>
						
					</li>
					<!-- <hr> -->

				</ul>
			</div>
		</div>
		<fooddetail></fooddetail>
		
	</div>
</template>

<script>
	import {mapGetters} from 'vuex'
	import BScroll from 'better-scroll'
	import fooddetail from '../detail/fooddetail.vue'
	import Cartcontrol from '../Cartcontrol/Cartcontrol.vue'
	
	// 
	export default{
		data(){
			return {
				listHeight : [],
				scrollY : 0
			}
		},
		computed : {
			...mapGetters([
				'goods'
			]),
			currentIndex(){
				for(let i = 0;i < this.listHeight.length ; i++){
					let heigth1 = this.listHeight[i];
					let height2 = this.listHeight[i+1];
					if(this.scrollY >= heigth1 && this.scrollY < height2){
						return i;
					}
				}
				return 0;
			},
		},
		created(){
			this.$store.dispatch('getGoods');
			this.$nextTick( () => {
				setTimeout( () => {
					this._initMenu();
					this._calcHeight();
					this._initFood();

				},100)
			})
		},
		methods : {
			selectMenu(k,e){
				// console.log(1);
				if( !e._constructed){
					return;
				}
				let foodList=this.$refs.menu2Wrapper.getElementsByClassName('food-list-hook');
				let el = foodList[k];
				this.foodBS.scrollToElement(el,300);
			},
			_calcHeight(){
				let foodList=this.$refs.menu2Wrapper.getElementsByClassName('food-list-hook');
				let height = 0;
				this.listHeight.push(height);
				for(let i = 0;i < foodList.length ; i++){
					height +=foodList[i].clientHeight;
					this.listHeight.push(height);
				}
				// console.log(this.listHeight);			
			},
			_initFood(){
				this.foodBS = new BScroll(this.$refs.menu2Wrapper,{
					probeType : 3,
					click:true
				});
				this.foodBS.on('scroll',(pos) =>{
					this.scrollY = Math.abs(Math.round(pos.y));
					// console.log(this,scrollY);
				})
			},

			_initMenu(){
				this.menuBS = new BScroll(this.$refs.menu1Wrapper,{
					click:true
				});
				// this.menuBS = new BScroll(this.$refs.menu2Wrapper);
			},
			showfood(f,e){
				// console.log(f.name);
				if( !e._constructed){
					return;
				};
				this.$store.dispatch('showfood',f);
			}

		},
		components:{
			fooddetail,
			Cartcontrol
			
		}
		
	}
</script>

<style lang='less' scoped>
*{
	box-sizing: border-box;
}
	.goods{
		position: absolute;
		top: 3.48rem;
		bottom: 0.92rem;
		width: 100%;
		display: flex;
		// z-index: -1;
		overflow: hidden;
		.goods-menu{
			// border:1px solid black;
			// height: 8.64rem;

			flex:0 0 1.6rem;
			width: 1.6rem;
			.menu-list{
				&.current{
					background:#fff;
				}
				height: 1.08rem;
				
				
				padding:0 0.24rem;
				display: table;
				.text{
					text-align: center;
					vertical-align: middle;
					display: table-cell;
					width: 1.12rem;
				}
			}
			ul li{

				
				font-size: 0.24rem;
				color: rgb(0, 0, 0);
				font-weight: 200;
				text-align: center;
				padding:0 0.24rem 0 0.24rem;
				background-color: #eee;
				span{

					
					border-bottom: 1px solid #ccc;
				}
				
			}

		}
		.goods-foods{
			flex:1;

			.foodmenu{
				color:rgb(147, 153, 159);
				font-size: 0.24rem;
				line-height: 0.52rem;
				padding-left: 0.28rem;
				heigth:0.52rem;
				border-left:2px solid #d9dde1;
				background-color: #f3f5f7;

			}
			.dish{
				// display: flex;
				.dishlist{
					display: flex;

					div:nth-of-type(2){
						width: 3.3rem;
						padding-left: 0.1rem;
					}
					div:nth-of-type(3){
						padding-top: 0.8rem;
						width: 1.2rem;
						text-align: center;

						color:rgb(0, 160, 220);
						// color:red;
						// position: relative;
						.carticon{
							// position: absolute;
							// right: 0.2rem;
						}

						// span{
						// 	font-size: 0.2rem;
						// 	color:rgb(147, 153, 159);
						// 	line-height: 0.48rem;
						// }
					}
					.iname{
						font-size: 0.28rem;
						color:rgb(7, 17, 27);
						line-height: 0.28rem;
					}
					.ides{
						font-size: 0.2rem;
						color:rgb(147, 153, 159);
						line-height: 0.25rem;
					}
					.isell{
						font-size: 0.2rem;
						color:rgb(147, 153, 170);
						line-height: 0.3rem;
					}
					.ipri{
						font-size: 0.28rem;
						color:red;
						font-weight: 700;
						line-height: 0.3rem;
					}
					.iold{
						font-size: 0.2rem;
						color:rgb(147,153,159);
						font-weight: 700;
						line-height: 0.48rem;
						text-decoration: line-through;
					}
				}
				li{	
					// display: flex;
					font-size: 0.24rem;
					margin: 0.2rem;
					border-bottom:1px solid #ddd;
				}
				li:last-child{
					border-bottom:none;
				}
				img{
					width: 1.1rem;
					height: 1.1rem;
				}
			}
		}
	}
</style>


