<template>
	<transition enter-active-class='animated slideInUp' leave-active-class='animated slideOutDown'>

	<div class="carfoods" >
		<div class="car">
			<div class="cartxt">
				购物车
			</div>
			<div class="clear" @click='clearfood'>
				清空
			</div>
		</div>
		<div>
			<div class="foodlist">
			<div class="listcon">
				<div v-for='v in carfood' class="listleft">
				<span>{{v.name}}</span>
				<div class="price">
					<div class="pri">￥{{v.price}}</div>
					<div class="add"><Cartcontrol :food='v'></Cartcontrol></div>
				</div>
				</div>
				
			</div>
				
				
			</div>
		</div>
	</div>
	</transition>

</template>

<script>
	import {mapGetters} from 'vuex'
	import Cartcontrol from '../Cartcontrol/Cartcontrol.vue'


	export default{
		// data(){
		// 	return{
		// 		b:-1,
		// 		c:0
		// 	}
		// },
		props:['carfood'],
		components:{
			Cartcontrol
		},
		methods:{
			clearfood(){
				
				this.carfood.forEach((food)=>{

				  	food.count=0;
				  	// this.b=!this.b
				})
				
				  
				  
				  // $(".carfoods").css("display","none")
				  // $(".carfoods").slideIn(500)

			}
		}
		
	}
</script>

<style lang='less' scoped>
	.carfoods{
		// display: none;
		width: 100%;
		// height: 2rem;
		background-color: #fff;
		position: fixed;
		bottom: 1rem;
		z-index: -1;
		.car{
			width: 100%;
			height: 0.8rem;
			background-color: #f3f5f7;
			display: flex;
			// border-bottom: 1px solid #ccc;
			.cartxt{
				font-size: 0.28rem;
				font-weight: 200;
				color:rgb(7,17,27);
				line-height: 0.8rem;
				padding-left: 0.3rem;
			}
			.clear{
				padding-left: 5.5rem;
				font-size: 0.24rem;
				color:rgb(0,160,220);
				line-height: 0.8rem;
			}
		}
		.foodlist{
			
			.listleft{
				span{
					width: 4.8rem;
				}
				display: flex;
				// width: 9rem;
				height: 0.66rem;
				margin: 0.3rem;
				font-size: 0.28rem;
				color:rgb(7,17,27);
				line-height: 0.48rem;
				border-bottom: 1px solid  #ddd;
				.price{
					display: flex;
					width: 2rem;
					text-align: center;
					// background-color: #ccc;
					.pri{
						font-size: 0.2rem;
						font-weight: 700;
						line-height: 0.48rem;
						color:rgb(240,20,20);
					}
					.add{
						margin-left: 0.2rem;
					}

				}
			}
			
		}
	}
</style>


