import type from '../type.js'
import axios from 'axios'

const state = {
	seller:{},
	detailshow :false
}

//返回数据
const getters={
	seller(state){
		return state.seller;
	},
	detailshow(state){
		return state.detailshow;
	}
}

const actions = {
	getSeller({commit,state}){
		axios.get('../../../static/data.json').then( resp => {
			if( resp.data.errno === 0){
				commit(type.GET_SELLER,resp.data.seller)
			}
		})
	},

	showdetail({commit}){
		commit(type.SHOW_DETAIL);
	},
	closedetail({commit}){
		commit(type.HIDE_DETAIL);
	}
}

const mutations = {
	[type.GET_SELLER](state,data){
		state.seller = data;
	},
	[type.SHOW_DETAIL](state,data){
		state.detailshow = true;
	},
	[type.HIDE_DETAIL](state,data){
		state.detailshow = false;
	}
}

export default {
	state,
	getters,
	actions,
	mutations
}


