<template>
<!-- <div class="bgblack"> -->
	

	<div class="shopcar" >
		<div class="carleft">

			<div class="car" >
				<div  class="cartotal">
					<div class="caricon" @click='scroll' :class='{"iconcol":totalCount}'>
						<i class="iconfont icon-gouwuche" :class='{"iconcol2":totalCount}'></i>
						
					</div>
				</div>

				

				<div class="num" v-if='totalCount'>
					<span>{{totalCount}}</span>
				</div>
				
			</div>
			<div class="cost">
				<span>￥{{totalPrice}}</span>
			</div>

			<div class='ship'>
				<span>另需配送费￥4元</span>
			</div>
		</div>

		<div class="carright" @click.stop.prevent='pay' :class='{"enough":totalPrice >= seller.minPrice}'>
			<p>{{payDesc}}</p>
		</div>

		<!-- <carfoods :carfood="selectFoods"  v-show='a'></carfoods> -->

		<transition enter-active-class='animated slideInUp' leave-active-class='animated slideOutDown'>

			<div class="carfoods"  v-show='carshow'  @click='scroll'>
			<div class="carbody">
				
			
				<div class="car">
					<div class="cartxt">
						购物车
					</div>
					<div class="clear" @click='clearfood'>
						清空
					</div>
				</div>

				<div>
				
					<div class="foodlist" ref="foodScroll">
						
							<div class="listcon">
								<div v-for='v in selectFoods' class="listleft">
									<span class="foodname">{{v.name}}</span>
									<div class="price">
										<div class="pri">￥{{v.price}}</div>
										<div class="add"><Cartcontrol :food='v'></Cartcontrol></div>
									</div>
								</div>
								
							</div>
						
						
					</div>
				</div>

				<div class="space">
					
				</div>

				</div>
			</div>
	</transition>
		
	</div>
<!-- </div> -->
</template>

<script>
	import  {mapGetters} from 'vuex'
	// import Vue from 'Vue'
	import Cartcontrol from '../Cartcontrol/Cartcontrol.vue'
	import BScroll from 'better-scroll'

	import carfoods from '../carfoods/carfoods.vue'


	export default {
		data(){
			return {
				a:false
				// b:false
				// ,
				// c:"totalCount!=0"

				
			}
		},
		// watch:{
		// 	goods(){
				
		// 	}
			
		// },
		// created(){
		// 	this.$nextTick(()=>{
		// 		setTimeout(()=>{
		// 			this._initFood()
		// 		},100)	
		// 	})

			
		// },
		
		
		computed:{
			...mapGetters([
				'seller',
				'goods'
			]),
			selectFoods(){
				let foods = [];
				this.goods.forEach(good => {
					good.foods.forEach(food=>{
						if(food.count>0){
							foods.push(food);
						}
					})
				})
				return foods;
			},
			totalPrice(){
				let total= 0;
				this.selectFoods.forEach(food=>{
					total+=food.price * food.count;
				});
				return total;
				
			},
			totalCount(){
				let count = 0;
				this.selectFoods.forEach(food=>{
					count += food.count;
				});
				return count;
			},
			payDesc(){
				if(this.totalPrice === 0){
					return `￥${this.seller.minPrice}元起送`
				}else if(this.totalPrice<this.seller.minPrice){
					return `还差${this.seller.minPrice - this.totalPrice}元起送`
				}else{
					return "去结算"
				}
			},
			carshow(){
				// console.log(1);
				// if( !e._constructed){
				// 	return;
				// };
				// this.$store.dispatch('carshow');
				// console.log(1)
				if(!this.totalCount){
					this.a=false;
					return false

				}
					return this.a;
				

				
			}


		},
		components:{
			carfoods,
			Cartcontrol
		},
		methods : {
			
			clearfood(){
				
				this.selectFoods.forEach((food)=>{

				  	food.count=0;
				  	
				})
				this.a=!this.a
				
				// this.b=!this.b
				// console.log(this.a)
				// console.log("----")
				// console.log(this.b)

			},
			// _initFood(){
			// 	this.BScroll = new BScroll(this.$refs.foodScroll,{
			// 		click:true
			// 	}); 
			// },
			scroll(){
				this.a=this.totalCount?!this.a:false;
				this.$nextTick(()=>{
					setTimeout(()=>{
						 if( !this.menuBS ){
                            this.menuBS= new BScroll(this.$refs.foodScroll, {
                                click: true
                            });
                        }else{
                            this.menuBS.refresh();
                        }

					},100)	
				})	
			}


		}
	}

</script>

<style lang='less'>

	.shopcar{
		position: fixed;
		bottom: 0;
		width: 100%;
		height: 1rem;
		background-color: #141d27;
		display: flex;
		z-index: 1000;
		.carleft{
			display: flex;
			width: 6rem;
			height: 1rem;
			background-color: #141d27;
			.num{
				width: 0.4rem;
				height: 0.25rem;
				border-radius: 0.4rem;
				background-color: red;
				position: fixed;
				left:0.9rem;
				bottom: 0.9rem;
				color:#fff;
				text-align: center;
				font-size: 0.16rem;
			}
			.cost{
				// font-weight: 800;
				width: 1.2rem;
				font-size: 0.38rem;
				line-height: 0.5rem;
				color:#919396;
				// padding-right: 0.2rem;
				// margin: 0.1rem;
				margin-top: 0.2rem;
				margin-bottom: 0.2rem;
				border-right:2px solid #2b333b;

			}
			.ship{
				font-size: 0.32rem;
				color: #919396;
				line-height: 1rem;
				padding-left: 0.3rem;
			}
			.car{
				// display: flex;
				width: 1rem;
				padding: 0.3rem;
				.cartotal{
					width: 1rem;
					height: 1rem;
					border-radius: 1rem;
					background-color: #141d27;
					// margin-top: 1rem;
					position: fixed;
					bottom: 0.2rem;
					text-align: center;
					line-height: 1rem;
					.caricon{
						&.iconcol{
							background-color: rgb(0,160,220);

						}
						width: 0.8rem;
						height: 0.8rem;
						position: fixed;
						bottom: 0.3rem;
						border-radius: 0.8rem;
						background-color: #2b343c;
						margin-left: 0.1rem;
						line-height: 0.7rem;
						.icon-gouwuche{
							&.iconcol2{
								color:#fff;
							}
							font-size: 0.48rem;
							color:#80858a;
							
						}

					}
					
				}
			}
		}
		.carright{
			width: 2.1rem;
			height: 1rem;
			background-color: #2b333b;
			text-align: center;
			line-height: 1rem;
			color:#979a9c;
			p{
				font-size: 0.24rem;
				font-weight: 700;
				
				
			}
			&.not-enough{
                background: red;
                color:#979a9c;
            }
			&.enough{
				color:#fff;
				background-color: green;
			}

		}

		.carfoods{

		// display: none;
			width: 100%;
			// height: 2rem;
			background-color: rgba(7,17,27,0.8);
			position: fixed;
			bottom: 0;
			top:0;
			left:0;
			right:0;
			z-index: -1;
		.carbody{
			width:100%;
			position:fixed;
			z-index:90;
			// height: 6.2rem;
			bottom:0;
			background-color:white;
		
		.car{
			width: 100%;
			height: 0.8rem;
			background-color: #f3f5f7;
			display: flex;
			// border-bottom: 1px solid #ccc;
			.cartxt{
				font-size: 0.28rem;
				font-weight: 200;
				color:rgb(7,17,27);
				line-height: 0.8rem;
				padding-left: 0.3rem;
			}
			.clear{
				padding-left: 5.5rem;
				font-size: 0.24rem;
				color:rgb(0,160,220);
				line-height: 0.8rem;
			}
		}
		.foodlist{
			max-height: 6rem;
			overflow: hidden;
			.listcon{
				max-height:100rem;
				
				.listleft{

				.foodname{
					width: 4.5rem;
				}
				display: flex;
				// width: 9rem;

				height: 0.66rem;
				margin: 0.3rem;
				font-size: 0.28rem;
				color:rgb(7,17,27);
				line-height: 0.48rem;
				border-bottom: 1px solid  #ddd;
				.price{
					display: flex;
					width: 2rem;
					text-align: center;
					// background-color: #ccc;
					.pri{
						width: 0.4rem;
						font-size: 0.2rem;
						font-weight: 700;
						line-height: 0.48rem;
						color:rgb(240,20,20);
					}
					.add{
						margin-left: 0.2rem;
					}

				}
			}
			}
			
			
		}
		.space{
			width: 100%;
			height: 1.4rem;
		}

	}
	}
	}


</style>


