<template>
	<div class="star">
		<span v-for='active in itemlist' class="cc" :class='active'></span>
	</div>
</template>

<script>
	const LENGTH = 5;
	const ON = 'on' ;
	const HALF = 'half';
	const OFF = 'off';

	export default {
		props : ['score'],
		computed : {
			itemlist(){
				let result = [];
				let integer = Math.floor(this.score);
				let decimal = this.score - integer;

				for(let i = 0 ; i < integer ; i++){
					result.push(ON);
				};

				if( decimal >= 0.5){
					result.push(HALF);
				};

				while (result.length < LENGTH ) {
					result.push(OFF);
				};

				return result;
			}
		}
	}
</script>

<style lang='less' scoped>
	.star{
		font-size: 0.5rem;
		.cc{
			display: inline-block;
			width: 0.5rem;
			height: 0.5rem;
			margin-right: 0.1rem;
			&.on{
				background-image: url('star_on.png');
				background-size: 0.5rem 0.5rem;
				background-repeat: no-repeat;
			}
			&.half{
				background-image: url('star_half.png');
				background-size: 0.5rem 0.5rem;
				background-repeat: no-repeat;
			}
			&.off{
				background-image: url('star_off.png');
				background-size: 0.5rem 0.5rem;
				background-repeat: no-repeat;
			}
		}
	}
</style>


