<template>
	<transition enter-active-class='animated fadeInLeft' leave-active-class='animated fadeOutRight'>
	<div class="detail" v-show='detailshow'>
		<div class="title">
			<p>粥品香坊(大运村)</p>
			<div class="star">

				<star :score= 'seller.score'></star>

			</div>
		</div>

		<div class="info">
			<hr>
			<p>优惠信息</p>
			<hr>

			

		</div>
		<div class="desc">
			<ul>
				<li v-for='(v,k) in seller.supports' :key='k'>
				<span class="icon" :class="iconClassMap[v.type]"></span>
				{{v.description}}
				</li>
			</ul>
		</div>
		
		<div class="announce">
			<hr>
			<p>商家公告</p>
			<hr>

			

		</div>
		<div class="desc">
			<p>{{seller.bulletin}}</p>
		</div>
		
		<div class="close">
			<i class="iconfont icon-X" @click='close'></i>
		</div>
		

		<!-- <input type="button" value="close" @click='close'  style='color:red;font-size:26px;'> -->
	</div>
	</transition>
</template>

<script>
	import {mapGetters} from 'vuex'
	import star from '../star/star.vue'

	export default {
		computed : {
			...mapGetters([
				'detailshow',
				'seller'
			])
		},
		components:{
			'star':star
		},
		methods : {
			close() {
				this.$store.dispatch('closedetail')
			}
		},
		created(){
			this.iconClassMap = ["decrease","discount","special","invoice","guarantee"]
		}
	}
</script>


<style lang='less' scoped>
	.detail{
		position: fixed;
		z-index:100000;
		top:0;
		left: 0;
		right: 0;
		bottom: 0;
		background:rgba(0, 0, 0, 0.8);

		.title p{
			width: 5rem;
			height: 0.32rem;
			text-align: center;
			margin: 0 auto;
			margin-top: 1.28rem;
			font-size: 0.32rem;
			font-weight: 700;
			color:rgb(255,255,255);
			line-height: 0.32rem;

		}
		.star{
				width: 6rem;
				margin: 0 auto;
				
				margin-top: 0.32rem;
				text-align: center;
				// opacity: 0;
				line-height: 0.48rem;
				
				
			}

		.info,.announce{
			width: 6rem;
			// height: rem;
			
			margin: 0 auto;
			text-align: center;
			margin-top: 0.56rem;
			// line-height: 0.5rem;
			display: flex;
			font-size: 0.32rem;
			font-weight: 700;
			color:rgb(255,255,255);

			hr{

				width: 2rem;
				height: 0.04rem;
				border:0;
				background-color: rgba(255, 255, 255, 0.2);
			}
		}
		.desc{
			margin: 0 auto;
			margin-top: 0.48rem;
			width: 5rem;
			font-size: 0.24rem;
			font-weight: 200;
			color:rgb(255, 255, 255);
			line-height: 0.24rem;
			p{
				line-height: 0.4rem;
			}
			li{
				margin-top:0.2rem;
			}

			.icon{
				display: inline-block;
				width: 0.32rem;
				height: 0.32rem;
				background-size: 0.32rem 0.32rem;
				&.decrease{
						background-image:url("../imgs/decrease.png");
						background-repeat: no-repeat;
				}
				&.discount{
						background-image:url("../imgs/discount.png");
						background-repeat: no-repeat;
				}
				&.special{
						background-image:url("../imgs/special.png");
						background-repeat: no-repeat;
				}
				&.invoice{
						background-image:url("../imgs/invoice.png");
						background-repeat: no-repeat;
				}
				&.guarantee{
						background-image:url("../imgs/guarantee.png");
						background-repeat: no-repeat;
				}
			}
			

		}
		.close{

			width: 0.5rem;
			text-align: center;
			margin: 0 auto;
			margin-top: 2.5rem; 
			color:rgba(255, 255, 255, 0.5);
			font-size: 0.64rem;
		}
		
	}
</style>


