<template>
	<div class="seller">
	<div class="menu-wrapper"  ref = "menuWrapper">
		<div class="total">
		
	
			<div class="title">
				<div class="brandtop">
				<div class="top">
					<div class="topleft">
						<p>{{seller.name}}</p>
						<div>
							<star :score='seller.score'></star>
							<span  class="sell">月售{{seller.sellCount}}单</span>
						</div>
						<!-- <p>{{seller.score}}
						
						</p> -->

					</div>

					<div class="topright" @click='show'>
						<i class="iconfont icon-xin1" ></i>
						<p class="collect">
							已收藏
						</p>
					</div>
					

				</div>
					
				</div>
					<div class="brandbottom">
						<div class="price">
							<p class="litter">起送价</p>
							<p class="num">{{seller.minPrice}}<span class="after">元</span></p>
						</div>

						<div class="minprice">
							<p class="litter">商家配送</p>
							<p class="num">{{seller.deliveryPrice}}<span class="after">元</span></p>
						</div>

						<div class="avgtime">
							<p class="litter">平均配送时间</p>
							<p class="num">{{seller.deliveryTime}}<span class="after">分钟</span></p>
						</div>
					</div>
				</div>

				<div class="strip"></div>
			
				<div class="notice">
					<div class="noticecon">
						<p>公告与活动</p>
						<p>{{seller.bulletin}}</p>
						<ul>
						<li v-for="v in seller.supports">
						<span class="icon" :class="iconClassMap[v.type]"></span>
						{{v.description}}
						</li>
						</ul>
					</div>
					
				</div>

				<div class="strip">
					
				</div>

				<div class="picture">
					<div>
						<p>商家实景</p>
						<div ref="menu2Wrapper" class="imgWrapper">
						<div>
							<ul>
								<li v-for='i in seller.pics'>
									<span><img :src="i" alt=""></span>
								</li>
							</ul>
						</div>
						</div>
					</div>
				</div>

				<div class="strip">
					
				</div>

				<div class="sellerinfo">
					<div>
						<p>商家信息</p>
						<ul>
							<li v-for='i in seller.infos'>{{i}}</li>
						</ul>
					</div>
					

				</div>

		</div>
	</div>

	</div>


</template>


<script>
	
	import {mapGetters} from "vuex"
	import BScroll from 'better-scroll'
	import star from '../star/star_s.vue'

	var i=1;

	export default {
		computed : {
			...mapGetters([
				// 'goods',
				'ratings',
				'seller'
			])
		},
		components:{
			'star':star
		},
		created(){
			// this.$store.dispatch('getGoods');
			this.$store.dispatch('getRatings');
			this.$store.dispatch('getSeller');
			this.$nextTick( () => {
				setTimeout( () => {
					this._initMenu();
				},100)
			});
			this.iconClassMap=["decrease","discount","special","invoice","guarantee"]


		},
		methods : {
			_initMenu(){
				// console.log(2);
				this.menuBS = new BScroll(this.$refs.menuWrapper,{
					click:true
				});
				this.menuBS= new BScroll(this.$refs.menu2Wrapper, {
                    // better-scroll 会将点击事件去掉，要在这里开启，同时点击在PC 会被执行两次，要在这里控制
                    scrollX: true,
                    click: true
                });
				
			},
			
			show(){
				// console.log(4);
				
				if(i==1){
					$(".icon-xin1").css("color","#ccc");
					$(".collect").html("收藏");
					i=0;
					// console.log(i);
				}else if(i==0){
					$(".icon-xin1").css("color","red");
					$(".collect").html("已收藏");
					i=1;
					// console.log(i);
				}
				
			}
		}
	}

</script>

<style lang='less' scoped>
	.seller{
		.menu-wrapper{
			height: 9.8rem;
			overflow: hidden;
		}
		.total{
			height:22rem;
			// overflow: hidden;
			// position: absolute;
			// z-index: -80;
		}
		.title{
			border-bottom: 2px solid #eee;
		}
		.brandtop{
			margin:0.36rem;
			border-bottom: 1px solid #ccc;
			height: 1.5rem;
			.sell{
				font-size: 0.24rem;
				display: inline-block;
				margin-top: -1.55rem;
				margin-left: 2rem;
				
			}
			.top{
				display: flex;
				.topleft{
					width: 6rem;
					p:nth-of-type(1){
					font-size: 0.28rem;
					color:rgb(7,17,17);
					line-height: 0.5rem;
					}
					
				}
				.topright{
					width: 2rem;
					text-align: center;
					font-size: 0.48rem;
					color: rgb(240, 20, 20);
					line-height: 0.48rem;
					p{
						font-size: 0.2rem;
						color:rgb(77, 85, 93);
						line-height: 0.2rem;
					}
				}
				

			}
		}
		.brandbottom{
				display: flex;
				margin:0.36rem;
				.litter{
					font-size: 0.2rem;
					color:rgb(147, 153, 159);
					line-height: 0.2rem;
				}
				.after{
					font-size: 0.2rem;
					color:rgb(7, 17, 27);
					line-height: 0.4rem;
				}
				p.num{
					font-size: 0.48rem;
					font-weight: 200;
					color: rgb(7, 17, 27);
					line-height: 0.48rem;
				}
				div{
					width: 2rem;
					text-align: center;
					border-left: 1px solid #ccc;
				}
				div:nth-of-type(1){
					border-left: none;
				}
		}
		.strip{
			width: 100%;
			height: 0.5rem;
			background-color: #eee;
		}
		.notice{

			.noticecon{
				margin: 0.36rem;
				.icon{
					display: inline-block;
					width: 0.32rem;
					height: 0.32rem;
					background-size:0.32rem 0.32rem;
					&.decrease{
						background-image:url("../imgs/decrease.png");
						background-repeat: no-repeat;
					}
					&.discount{
						background-image:url("../imgs/discount.png");
						background-repeat: no-repeat;
					}
					&.special{
						background-image:url("../imgs/special.png");
						background-repeat: no-repeat;
					}
					&.invoice{
						background-image:url("../imgs/invoice.png");
						background-repeat: no-repeat;
					}
					&.guarantee{
						background-image:url("../imgs/guarantee.png");
						background-repeat: no-repeat;
					}
				}
				p:nth-of-type(1){
					font-size:0.28rem;
					color: rgb(7, 17, 27) ;
					line-height: 0.5rem;
				}
				p:nth-of-type(2){
					font-size:0.24rem;
					font-weight: 200;
					color: rgb(240, 20, 20) ;
					line-height: 0.48rem;
				}
				ul{
					border-top: 1px solid #ddd;
				}
				ul li{
					font-size: 0.24rem;
					font-weight:200;
					color:rgb(7, 17, 27) ;
					line-height: 0.96rem;
					height: 0.96;
					border-bottom: 1px solid #ddd;

				}
				
			}
		}
		.picture{
			padding:0.3rem;
			    .imgWrapper{
		            width:7rem;
		            overflow:hidden;
		            div{
		                width:10rem;
		                ul{
		                    display:flex;
		                    padding-bottom:0.36rem;
		                    li{
		                        padding-left:0.12rem;
		                        img{
		                            width:2.4rem;
		                            height:1.8rem;
		                        }
		                    }
		                }
		            }
		        }
			
			p{
				font-size: 0.4rem;
				line-height: 0.5rem;
			}
			
		}

		.sellerinfo{
			div{
				padding:0.3rem;
				
				p{  
					line-height: 0.7rem;
					font-size: 0.4rem;
				}
				ul li{
					font-size: 0.24rem;
					font-weight: 200;
					color:rgb(7,17,27);
					height: 0.8rem;
					line-height: 0.8rem;
					border-bottom:1px solid #ddd;
				}
				ul li:last-child{
					border-bottom:none;
				}
			}
		}
	}
</style>


