import type from '../type.js'
import axios from 'axios'

const state = {
	ratings:[],
	
}

//返回数据
const getters={
	ratings(state){
		return state.ratings;
	}
}

const actions = {
	getRatings({commit,state}){
		axios.get('../../../static/data.json').then( resp => {
			if( resp.data.errno === 0){
				commit(type.GET_RATINGS,resp.data.ratings)
			}
		})
	}
}

const mutations = {
	[type.GET_RATINGS](state,data){
		state.ratings = data;
	}
}

export default {
	state,
	getters,
	actions,
	mutations
}


