<template>

	<div  class="menu-foods" >
	<transition enter-active-class='animated slideInLeft' leave-active-class='animated slideOutRight'>
		<div class="goodsdetail" v-show="foodshow" ref = "menu1Wrapper">
			<div class="detail-content">
				<div class="image-header">
					
						<img :src="food.image" alt="">
					
					
				</div>
				<div class="cc" @click="hidefood">
					<i class="iconfont icon-shiliangzhinengduixiang4"></i>
				</div>

				<div class="foodinfo">
					<div>
						<p>{{food.name}}</p>
						<span>月售{{food.sellCount}}份&nbsp;好评率{{food.rating}}%</span>
						
						<p>￥{{food.price}}&nbsp;<span>{{food.oldPrice}}</span></p>

						<div class="cars">
							<div  class="addcar1">
								<Cartcontrol :food="food"></Cartcontrol>
							</div>
						
							<div class="addcar" @click='add' v-show='!food.count'>
							
							加入购物车
							</div>
						</div>
						
					</div>
				</div>

				<div class="strip">
					
				</div>

				<div class="foodintr">
					<p>商品介绍</p>
					<div>
						<p>{{food.info}}</p>
					</div>
				</div>

				<!-- 评论 -->
				<div class="conrating">
					<div class="but">
						<div class="btn1" @click="getall">全部 <span></span></div>
						<div class="btn2" @click="ok">满意 <span></span></div>
						<div class="btn3" @click="no">不满意 <span></span></div>
						
					</div>

					<div class="sel" >
							<i class="iconfont icon-duihao"  @click="changeFlag" :class="{'checked':flag}"></i>
							<span :class="{'checked':flag}">只看有内容的评价</span>
					</div>
					
						<div class="user">
							<div class="userrat" v-for='(v,k) in ratList' :key='k'  v-show="all || yes === v.rateType">
								
									
									<div class="userright">
										<span class="rate">{{changeTime(v.rateTime)}}</span>
										<!-- <p><star :score='v.score'></star></p> -->
										<p>
										<!-- <i class="iconfont icon-damuzhion"></i> -->
										<i v-if="v.rateType == 0" class="iconfont icon-damuzhion"></i>
										<i v-else="v.rateType == 1" class="iconfont icon-down no"></i>
										{{v.text}}
										</p>
										
									</div>
									<div class="name">
										<span>{{v.username}}</span><img :src="v.avatar" alt="">
									</div>
								
								
							</div>
							
						</div>
					
				</div>
				
				<div class="space"></div>
			</div>
			<!-- <shopcar></shopcar> -->
		
			
		</div>
		</transition>
	</div>

</template>

<script>
	import {mapGetters} from 'vuex'
	import star from '../star/star_s.vue'
	import BScroll from 'better-scroll'
	import shopcar from '../shopcar/shopcar.vue'
	import Cartcontrol from '../Cartcontrol/Cartcontrol.vue'
	import Vue from 'vue'


	export default {
		data(){
			return {
				all:true,
				yes:-1,
				flag:false
			}
		},
		computed : {
			...mapGetters([
				'foodshow',
				'food'
			]),
			ratList(){
				if(this.flag){
					return this.food.ratings.filter((item)=>{
						return item .text
					})
				}
				return this.food.ratings;
			}
		},
		components:{
			'star':star,
			shopcar,
			Cartcontrol
		},
		watch:{
            food(){
                this.$nextTick( () => {
                    setTimeout( ()=>{
                        if( !this.menuBS ){
                            this.menuBS= new BScroll(this.$refs.menu1Wrapper, {
                                click: true
                            });
                        }else{
                            this.menuBS.refresh();
                        }
                    },1000)    
                });
            }
        },

		methods : {
			hidefood(){
				this.$store.dispatch('hidefood');
			},
			add(){
					Vue.set(this.food , 'count' ,1);
			},
			// _initMenu(){
			// 	this.menuBS = new BScroll(this.$refs.menu1Wrapper,{
			// 		click:true
			// 	});
				
			// },
			refresh(){
				// console.log(1);
				setTimeout(() => {
					this.menuBS.refresh()
				},20)
			},
			ok(){
				// console.log(1)
				this.yes = 0
				this.all = false
				this.refresh()
			},
			getall(){
				// console.log(1)
				this.yes = -1
				this.all = true
				this.refresh()
			},
			no(){
				// console.log(1)
				this.yes = 1
				this.all = false
				this.refresh()
			},
			changeTime(time){
				let time1 = new Date(time);
				let year = time1.getFullYear();
				let month = time1.getMonth();
				let date = time1.getDate();
				let hour = time1.getHours();
				let minute = time1.getMinutes();
				let seconds = time1.getSeconds();
				let timechange = year+"-"+month+"-"+date+"-"+hour+":"+minute+":"+seconds;
				return timechange;
			},
			changeFlag(e){
				if( !e._constructed ){
					return;
				};
				this.flag = !this.flag;
				this.refresh();
				// console.log(111)
			}

		}
	}
</script>

<style lang="less" scoped>


.menu-foods{
	// height: 7rem;
	// background-color: #fff;
	

	.goodsdetail{
		// background-color: pink;
		
		// height: 22rem;
		background-color: #fff;
		position:fixed;
		top: 0;
		z-index:4;
		left: 0;
		right: 0;
		bottom: 0;

		.detail-content{
			.space{
				width: 100%;
				height: 1.16rem;
			}
			.image-header{
				position: relative;
				width: 100%;
				height: 0;
				padding-bottom: 100%;
				img{
					position: absolute;
					width: 100%;
					height: 100%;
					left: 0;
					top:0;
				}
			}
			.cc{
				position: fixed;
				top:-10px;
				left: 10px;
				width: 10px;
				height: 10px;
				// border: 1px solid red;
				padding:10px;
				color: white;
			}
		}

		.foodinfo{padding: 0.3rem;
			div{
				height: 2rem;
				p:nth-of-type(1){
					font-size: 0.28rem;
					font-weight: 700;
					color:rgb(7, 17, 27);
					line-height: 0.28rem;
				}
				span:nth-of-type(1){
					font-size: 0.2rem;
					color:rgb(147, 153, 159);
					line-height: 0.2rem;
				}
				p:nth-of-type(2){
					font-size: 0.2rem;
					font-weight: 700;
					color: rgb(240, 20, 20);
					line-height: 0.48rem;
					span{
						font-size: 0.2rem;
						color: rgb(147, 153, 159);
						line-height: 0.48rem;
						text-decoration: line-through;
					}
				}
			}
			border-bottom: 1px solid rgba(7, 17, 27, 0.1);
			.cars{
				position: relative;
			.addcar{
				width: 1.6rem;
				height: 0.7rem;

				background-color: rgb(0, 160, 220);
				border-radius: 0.48rem;
				font-size: 0.2rem;
				// color:black;
				color: rgb(255, 255, 255);
				line-height: 0.7rem;
				text-align: center;
				margin-top:-0.7rem;
				margin-left: 5rem; 
				position: absolute; 
			}
			.addcar1{
				// width: 2rem;
				// height: 0.1rem;
				// background-color: #ccc;
				// border-radius: 0.48rem;
				font-size: 0.2rem;
				color:#00a0dc;
					
				// color: rgb(255, 255, 255);
				// line-height: 0.1rem;
				text-align: center;
				margin-top:-0.55rem;
				margin-left: 5rem;
				position: absolute; 
			}
			}
			
		}
		.strip{
			height: 0.32rem;
			background-color: #f3f5f7;
		}

		.foodintr{
			padding: 0.3rem;
			p:nth-of-type(1){
				font-size: 0.28rem;
				font-weight: 700;
				color:rgb(7, 17, 27);
				line-height: 0.28rem;
			}
			div{
				padding: 0.16rem;
				p:nth-of-type(1){
					font-size: 0.24rem;
					font-weight: 200;
					color: rgb(77, 85, 93);
					line-height: 0.48rem;
				}
			}
		}
		.conrating{
		// height: 18rem;
		background-color: #fff;
		// position: absolute;
		// z-index: -10;
		.but{
			width: 7rem;
			// background-color: #ccc;
			margin: 0.2rem auto;
			display: flex;
			font-size: 0.24rem;

			border-bottom: 1px solid #ddd;

			div{
				width: 1.5rem;
				height: 0.8rem;
				text-align: center;
				line-height: 0.8rem;
				margin-right: 0.3rem;
				margin-bottom: 0.3rem;

				// background-color: red;
			}
			div:nth-of-type(1){

				background-color: skyblue;
			}

			div:nth-of-type(2){

				background-color: lightblue;
			}
			
			div:nth-of-type(3){

				background-color: #ddd;
			}
			.btn1:active,.btn2:active,.btn3:active{
				background-color:#52bbf2;
			}
		}
		
		.sel{
			// width: 100%;
			height: 0.6rem;
			padding-left: 0.3rem;
			color:#ccc;
			font-size: 0.3rem;
			border-bottom: 1px solid #ddd;
			.checked{
				color:skyblue;
			}
			// .checked2{
			// 	color:#aaa;
			// }
		}
		.user{
			background-color: #fff;

		}
		.userrat{
			border-bottom: 1px solid #ddd;
			margin: 0.2rem;
			display: flex;
			height: 1.6rem;
			
			img{
				width: 0.36rem;
				border-radius: 0.5rem;
			}
			p:nth-of-type(1){
				font-size: 0.2rem;
				line-height: 0.3rem;
			}
			span:nth-of-type(1){
				font-size: 0.2rem;
				color: rgb(147, 153, 159);
				line-height: 0.4rem;
			}
			.userright{
				width: 4rem;
				.icon-damuzhion{
					color:skyblue;

				}
				.icon-down{
					color:#ccc;
				}
			}
			.name{
				margin-left: 2rem;
			}
			
		}

	}
	

	}
	
}
</style>


